body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.TodoCounter{
  font-size: 24px;
  text-align: center;
}
.form-control{
  text-align: center;
}
.CreateTodoButton {
  z-index: 1;
}
.list-group{
  margin-top: 50px;
}
.Icon:hover{
  cursor: pointer;
}
.TodoItem{
  text-align: left;
}
.TodoItem .Icon{
  display: block;
  margin-right: 20px;
}
.TodoItem .Icon-check{
  float: left;
}
.TodoItem .Icon-delete{
  float: right;
}
.list-group-item-success{
  text-decoration: line-through;
}
.ModalBackground {
  background: rgba(32,35,41,.8);
  position: fixed;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}
.ModalBackground .contanier{
  background-color: white;
  width: 400px;
  height: 300px;
  text-align: center;
  padding-top: 60px;
  border-radius: 45px;
}

.ModalBackground .contanier h2{
  color:darkslateblue;
}

.ModalBackground .contanier textarea{
  padding: 15px;
  width: 300px;
  height: 100px;
  border: solid 1px #ccc;
}

.ModalBackground .contanier button{
  padding:5px;
  margin: 0 20px;
}
body{
  background: rgb(71, 75, 179);
  padding:40px;
}
#root{
  max-width: 600px;
  background: linear-gradient(to bottom left, blue, white);
  border:solid 1px #ccc;
  padding:40px;
  margin-top:50px;
  border-radius: 45px;
  text-align: center;
}

.CreateTodoButton {
  background-color: #27aa5a;
  box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  font-size: 50px;
  position: fixed;
  bottom: 24px;
  right: 24px;
  font-weight: bold;
  color: #FAFAFA;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 64px;
  width: 64px;

  -webkit-transform: rotate(0);

          transform: rotate(0);
  transition: .3s ease;
}

.CreateTodoButton:hover {
  -webkit-transform: rotate(224deg);
          transform: rotate(224deg);
}
